import React, { Component } from "react";
import Header from "../src/components/Header";

export class Week1 extends Component {
  async componentDidMount() {
    // this.fetchData();
  }
  fetchData = async () => {
    const response = await fetch("https://data.ntpc.gov.tw/api/v1/rest/datastore/382000000A-000139-001", {
      method: "GET",
      credentials: "include"
    });
    if (response.ok) {
      const data = await response.json();
      console.log(data);
    } else {
      console.log(response);
    }
  };
  render() {
    return (
      <div>
        <Header title={"蕃茄鐘 ｜ Week 1"} />
        <button onClick={this.fetchData}>button fetch</button>
      </div>
    );
  }
}

export default Week1;
